<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-expansion-panel hover focusable inset >
    <v-expansion-panel-header>
      <h4>{{ place }}</h4>
    </v-expansion-panel-header>

    <v-expansion-panel-content>
      <v-row dense >
        <v-col cols="12" md="4"
               v-for="memo in filteredMemos">
          <iframe width="100%" height="333px"
                  :src="mkUrl(memo)"/>
        </v-col>
      </v-row>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
  const providerUrls = {
    more (stop) {
      return 'https://www.morebus.co.uk/stops/' + stop;
    },
    yellow (stop) {
      return 'https://www.yellowbuses.co.uk/stops/' + stop;
    }
  };
  const providerActivity = {
    more  : true,
    yellow: false
  };

  export default {
    name: 'BusesPlace',
    props: ['memos', 'active', 'place'],

    computed: {
      filteredMemos() {
        return this.memos.filter(memo =>
          providerActivity[this.providerAndId(memo)[1]] === this.active);
      }
    },

    methods: {
      mkUrl(memo) {
        const split = this.providerAndId(memo);
        return providerUrls[split[1]](split[2]);
      },

      providerAndId(memo) {
        // TODO Unsafety in case of provider missing
        return memo.match('(.+)~(.+)');
      }
    }
  }
</script>
